import { CqsItem } from "@rp/common/api-types"
import { Watchlist } from "@rp/common/api-types"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { Collapse, Link, Typography } from "@rp/react/web/mui/core"
import { ExpandLess, ExpandMore } from "@rp/react/web/mui/icons"
import { FilterDisplay } from "../FilterDisplay"
import { CategoryWidgetActions, WidgetCategory } from "../hooks/useWidgetCategories"
import { GroupFamilyStyled } from "./Group.styles"

type Props = {
  widgetCategory: WidgetCategory
  filters: Array<CqsItem>
  label: string
  onChange?: (item: CqsItem) => void
  onClick?: (item: CqsItem) => void
  onExclude?: (item: CqsItem) => void
  widgetActions?: CategoryWidgetActions
  disableCheckbox?: boolean
  disableWatchlist?: boolean
  onAddToWatchlist?: (watchlist: Watchlist) => void
}

const GroupFamily = ({
  filters,
  onChange,
  onClick,
  onExclude,
  label,
  widgetCategory,
  widgetActions,
  disableCheckbox,
  disableWatchlist,
  onAddToWatchlist,
}: Props) => {
  const open = !!widgetCategory.unfoldedFamilies.find(item => item === label)

  return (
    <GroupFamilyStyled>
      <Typography sx={{ gap: "12px" }}>
        <Link
          onClick={() => widgetActions && widgetActions.toggleFamilyFold(widgetCategory.id, label)}
        >
          {open ? <ExpandLess /> : <ExpandMore />}
          <EllipsisTooltip tooltipText={label}>{label}</EllipsisTooltip>
        </Link>
      </Typography>
      <Collapse in={open} timeout={50}>
        <FilterDisplay
          showMore={widgetCategory.showMore.find(item => item === label) !== undefined}
          toggleShowMore={() =>
            widgetActions && widgetActions.toggleShowMore(widgetCategory.id, label)
          }
          filters={filters}
          onChange={onChange}
          onClick={onClick}
          onExclude={onExclude}
          disableCheckbox={disableCheckbox}
          onAddToWatchlist={onAddToWatchlist}
          disableWatchlist={disableWatchlist}
        />
      </Collapse>
    </GroupFamilyStyled>
  )
}

export { GroupFamily }
